

body{
  background-color: white
}
.App{
  background-color: #dbd7e6;
  min-height:500px;
  width: 400px;
  margin: 40px auto;
}

#to-do-form input{
  background-color: rgb(236, 241, 243);
  border: 0; 
  color: black;
  width: 200px;
  height: 50px;
  margin: 20px;
  padding: 0 20px;
  font-size: 18px;
  border-radius: 10px;
}
.addbutton{
  display: none;
}
#to-do-form input::placeholder{
  color: rgba(255,255,255,0.5);
}
#to-do-form input:focus{
  outline: none;
}
#to-do-form button{
  height: 50px;
  width: 80px;
  border-radius: 5px;
  border: 0;
  background-color:rgb(214, 210, 200); 
  cursor: pointer;
}
#to-do-form button:focus{
  outline: none;
}
#btn{
  height: 50px;
  width: 150px;
  display: flex;
  justify-content: center;
  text-align: center;
  background-color: rgba(255,255,255,0.5);
  border: none;
  border-radius: 5px;
}